media (min-width: 768px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n@mixin desktop {\r\n    @media (min-width: 1024px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// Component\r\n.title-section {\r\n    text-align: center;\r\n    margin-bottom: 2rem;\r\n\r\n    & p {\r\n        font-weight: 500;\r\n    }\r\n}\r\n\r\nloader-ring {\r\n    display: grid;\r\n    place-content: center;\r\n}\r\n\r\n.lds-ring {\r\n    margin: 2rem auto;\r\n    display: none;\r\n    position: relative;\r\n    width: 60px;\r\n    height: 60px;\r\n}\r\n\r\n.lds-ring div {\r\n    box-sizing: border-box;\r\n    display: block;\r\n    position: absolute;\r\n    width: 44px;\r\n    height: 44px;\r\n    margin: 6px;\r\n    border: 6px solid #dfdfdf;\r\n    border-radius: 50%;\r\n    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\r\n    border-color: #dfdfdf transparent transparent transparent;\r\n}\r\n\r\n.lds-ring div:nth-child(1) {\r\n    animation-delay: -0.45s;\r\n}\r\n\r\n.lds-ring div:nth-child(2) {\r\n    animation-delay: -0.3s;\r\n}\r\n\r\n.lds-ring div:nth-child(3) {\r\n    animation-delay: -0.15s;\r\n}\r\n\r\n@keyframes lds-ring {\r\n    0% {\r\n        transform: rotate(0deg);\r\n    }\r\n    100% {\r\n        transform: rotate(360deg);\r\n    }\r\n}\r\n\r\n// Persection\r\n.skip-link {\r\n    position: absolute;\r\n    top: -40px;\r\n    left: 0;\r\n    background-color: #023047;\r\n    padding: 8px;\r\n    z-index: 100;\r\n}\r\n\r\n.skip-link:focus {\r\n    top: 0;\r\n}\r\n\r\n// ini bagian header\r\n.header {\r\n    position: fixed;\r\n    top: 0;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    padding-top: 0.4rem;\r\n    padding-bottom: 0.4rem;\r\n    width: 100%;\r\n    z-index: 10;\r\n\r\n    &::before {\r\n        content: '';\r\n        position: absolute;\r\n        top: 0;\r\n        left: 0;\r\n        height: 100%;\r\n        width: 100%;\r\n        background-color: rgba(0, 0, 0, 0.245);\r\n        backdrop-filter: blur(30px);\r\n        z-index: -5;\r\n    }\r\n}\r\n\r\n.header-wrapper {\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.header__logo-wrapper,\r\n.footer__logo-wrapper {\r\n    align-items: center;\r\n}\r\n\r\n.header__logo-img,\r\n.footer__logo-img {\r\n    height: 2.5rem;\r\n}\r\n\r\n.header__logo-name,\r\n.footer__logo-name {\r\n    margin: 0.2rem 0 0 0.5rem;\r\n    font-weight: 600;\r\n}\r\n\r\n.header__navbar {\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 0;\r\n    display: block;\r\n    width: 100%;\r\n    height: 0;\r\n    background-color: rgba(0, 0, 0, 0.245);\r\n    backdrop-filter: blur(20px);\r\n    text-align: center;\r\n    overflow: hidden;\r\n    transition: ease 0.3s;\r\n    -webkit-transition: ease 0.3s;\r\n    -moz-transition: ease 0.3s;\r\n    -ms-transition: ease 0.3s;\r\n    -o-transition: ease 0.3s;\r\n}\r\n\r\n.header__navbar.active-navbar {\r\n    height: auto;\r\n    padding: 0.8rem 0;\r\n}\r\n\r\n.btn-humburger,\r\n.btn-x-navbar {\r\n    padding: 0.4rem 0 0.4rem 1.9rem;\r\n    font-size: 1.5rem;\r\n    background-color: transparent;\r\n    border: none;\r\n}\r\n\r\n.btn-humburger.active-navbar {\r\n    display: none;\r\n}\r\n\r\n.btn-x-navbar {\r\n    display: none;\r\n}\r\n\r\n.btn-x-navbar.active-navbar {\r\n    display: block;\r\n}\r\n\r\n.header__navbar-list {\r\n    display: block;\r\n    list-style: none;\r\n    transform: translateY(-50px);\r\n    -webkit-transform: translateY(-50px);\r\n    -moz-transform: translateY(-50px);\r\n    -ms-transform: translateY(-50px);\r\n    -o-transform: translateY(-50px);\r\n    transition: ease 0.5s;\r\n    -webkit-transition: ease 0.5s;\r\n    -moz-transition: ease 0.5s;\r\n    -ms-transition: ease 0.5s;\r\n    -o-transition: ease 0.5s;\r\n}\r\n\r\n.header__navbar.active-navbar .header__navbar-list {\r\n    transform: translateY(0);\r\n    -webkit-transform: translateY(0);\r\n    -moz-transform: translateY(0);\r\n    -ms-transform: translateY(0);\r\n    -o-transform: translateY(0);\r\n}\r\n\r\n.header__navbar-link {\r\n    display: inline-block;\r\n    padding: 0.8rem 0;\r\n    font-weight: 500;\r\n    opacity: 0.8;\r\n    transition: all 0.3s ease;\r\n\r\n    &:hover {\r\n        color: #ffb703;\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n// ini bagian hero\r\n.hero {\r\n    background: url('../public/images/heros/hero-image_4-small.jpg');\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    background-position: center;\r\n\r\n    @include tablet {\r\n        background-image: url('../public/images/heros/hero-image_4-medium.jpg');\r\n    }\r\n\r\n    @include desktop {\r\n        background-image: url('../public/images/heros/hero-image_4-large.jpg');\r\n    }\r\n}\r\n\r\n.hero__wrapper {\r\n    width: 100%;\r\n    height: 100vh;\r\n    justify-content: center;\r\n}\r\n\r\n.hero__description {\r\n    margin: 0.8rem 0 1rem;\r\n    line-height: 1.8rem;\r\n}\r\n\r\n.hero__total-user {\r\n    align-items: center;\r\n}\r\n\r\n.total-user__image {\r\n    width: 2.25rem;\r\n    height: 2.25rem;\r\n    border: 1.5px solid white;\r\n    border-radius: 50px;\r\n    -webkit-border-radius: 50px;\r\n    -moz-border-radius: 50px;\r\n    -ms-border-radius: 50px;\r\n    -o-border-radius: 50px;\r\n    overflow: hidden;\r\n}\r\n\r\n.total-user__image:not(:first-of-type) {\r\n    margin-left: -0.7rem;\r\n    display: grid;\r\n    place-items: center;\r\n    color: white;\r\n    line-height: 0;\r\n}\r\n\r\n.total-user__image img {\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n.total-user__image:last-of-type {\r\n    background-color: #df6951;\r\n}\r\n\r\n.total-user__description {\r\n    margin-left: 0.5rem;\r\n    font-size: 0.8rem;\r\n    line-height: 1rem;\r\n    opacity: 0.9;\r\n}\r\n\r\n.hero__nav {\r\n    margin-top: 0.5rem;\r\n    padding: 1rem 0;\r\n    font-weight: 500;\r\n    text-decoration: underline;\r\n    text-decoration-color: #ffb703;\r\n    -moz-text-decoration-color: #ffb703;\r\n}\r\n\r\n.hero__nav i {\r\n    margin-left: 0.4rem;\r\n}\r\n\r\n// ini adalah bagian section info\r\n.info__wrappper-image--mobile,\r\n.info__wrappper-image {\r\n    width: 70%;\r\n    position: relative;\r\n    margin: 3rem auto 2rem;\r\n    text-align: center;\r\n}\r\n\r\n.info__wrappper-image {\r\n    display: none;\r\n}\r\n\r\n.info__img {\r\n    position: relative;\r\n    width: 100%;\r\n}\r\n\r\n.info__img-circle-one {\r\n    position: absolute;\r\n    top: -3%;\r\n    left: 31%;\r\n    width: 15%;\r\n    border-radius: 50%;\r\n    -webkit-border-radius: 50%;\r\n    -moz-border-radius: 50%;\r\n    -ms-border-radius: 50%;\r\n    -o-border-radius: 50%;\r\n}\r\n\r\n.info__img-circle-two {\r\n    position: absolute;\r\n    top: -2;\r\n    left: -18%;\r\n    width: 46.89%;\r\n    border-radius: 50%;\r\n    -webkit-border-radius: 50%;\r\n    -moz-border-radius: 50%;\r\n    -ms-border-radius: 50%;\r\n    -o-border-radius: 50%;\r\n}\r\n\r\n.info__img-circle-three {\r\n    position: absolute;\r\n    top: 35%;\r\n    left: -25%;\r\n    width: 28.5%;\r\n    border-radius: 50%;\r\n    -webkit-border-radius: 50%;\r\n    -moz-border-radius: 50%;\r\n    -ms-border-radius: 50%;\r\n    -o-border-radius: 50%;\r\n    box-shadow: 0px 56.41608px 87.75835px 0px rgba(1, 22, 24, 0.24);\r\n}\r\n\r\n.info__description {\r\n    line-height: 1.8rem;\r\n    opacity: 0.9;\r\n    font-weight: 500;\r\n}\r\n\r\n// ini bagian layanan unggulan dan jenis makanan\r\n.layanan {\r\n    background-color: #fff8ef;\r\n}\r\n\r\n.layanan__wrapper-card,\r\n.jenis-kuliner__wrapper-card {\r\n    flex-wrap: wrap;\r\n    align-items: normal;\r\n    gap: 1rem;\r\n}\r\n\r\n.card-small {\r\n    flex: 1;\r\n    flex-basis: 100%;\r\n    padding: 2.2rem 1.2rem;\r\n    border-radius: 0.5rem;\r\n    -webkit-border-radius: 0.5rem;\r\n    -moz-border-radius: 0.5rem;\r\n    -ms-border-radius: 0.5rem;\r\n    -o-border-radius: 0.5rem;\r\n}\r\n\r\n.card-layanan {\r\n    background-color: white;\r\n    border: 1px solid #c5c2c2;\r\n\r\n    & 